.AllGames {
  width: 125%;
}

.GameCell {
  background-color: rgb(199, 144, 225);
  margin: 0 auto;
  border-radius: 10px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 20px;
  padding: 3%;
}
.GameCell-link:hover {
  border-style: solid;
  border-width: 20px 5px;
}